import { percent } from '../../utils/transform/percent';

/**
 * 
 * @param options 
 */
export function getAreaData(
  data: Record<string, any>[],
  yField: string,
  groupField: string,
  asField: string,
  isPercent?: boolean
) {
  return !isPercent ? data : percent(data, yField, groupField, asField);
}
